body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'radnika';
  src: url(/static/media/radnika-medium-webfont.352f4137.woff2) format('woff2'),
       url(/static/media/radnika-medium-webfont.0474c626.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

.first-section {
background: linear-gradient(
    rgba(0, 0, 0, 0.3),
    rgba(0, 0, 0, 0.3)
  ), url(/static/media/img2.960e7a28.jpg), no-repeat center center fixed;
background-size: cover;
background-position: center;
}

body {
background: #ECE9E6;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.sponsor-container {
display: flex;
position: relative;
align-items: center;
}

/* Centered text */
.sponsor-centered {
color: #fff;
text-align: center;
width: 100vw;
opacity: 0.9;
text-shadow: 2px 2px grey;
font-size: 5em;
margin: 0px auto;
position: absolute;
left: 0;
right: 0;
}

.regular-text {
font-size: 1.2em;
line-height: 28px;
}

